.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.week6card{
  background-color:lightblue;
  color:forestgreen;
  font-style:italic;
  font-family:'Times New Roman', Times, serif;
  font-size: medium;
  height:1700px;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  align-items: center;
  
  
}

  

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content:space-between;
   border: 50px;
  border-style: solid; 
  
}

.card { /* Adjust as needed for spacing */
  margin-bottom: 20px; 
  background-color:antiquewhite;
  /* border-radius: 8px; */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.5s ease;
  width: 250px;
  height: 380px;
}

.card:hover {
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2), /* Original shadow */
              0 16px 18px rgba(82, 2, 2, 0.2); /* Additional shadow */
}


.card-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.card-text {
  font-size: 1rem;
  /* margin-bottom: 0.3rem; */
}

.button {
  display: inline-block;
  padding: 1rem 2rem;
  background-color: #007bff;
  color:chartreuse;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  justify-content: space-between;
  text-align: center;
  transition: background-color 0.3s ease;
  
}

.button:hover {
  background-color: #0056b3;
}

